"use client";

import type { ReactNode } from "react";

import { AnalyticsProvider } from "@/components/AnalyticsProvider";
import { ConsoleSignature } from "@/components/ConsoleSignature";

export function Providers({ children }: { children: ReactNode }) {
  return (
    <AnalyticsProvider>
      <ConsoleSignature />
      {children}
    </AnalyticsProvider>
  );
}

