"use client";

import type { ReactNode } from "react";
import { useEffect } from "react";
import { usePathname } from "next/navigation";
import posthog from "posthog-js";
import { PostHogProvider } from "posthog-js/react";

let didInit = false;

function PostHogInit() {
  useEffect(() => {
    if (didInit) return;

    const key = process.env.NEXT_PUBLIC_POSTHOG_KEY;
    const host = process.env.NEXT_PUBLIC_POSTHOG_HOST;

    if (!key || !host) {
      if (process.env.NODE_ENV === "development") {
        // eslint-disable-next-line no-console
        console.warn(
          "[PostHog] Missing NEXT_PUBLIC_POSTHOG_KEY or NEXT_PUBLIC_POSTHOG_HOST. Analytics disabled."
        );
      }
      return;
    }

    posthog.init(key, {
      api_host: host,
      person_profiles: "identified_only",
      // App Router: we capture pageviews on navigation ourselves.
      capture_pageview: false,
      session_recording: {
        captureCanvas: {
          recordCanvas: true,
          canvasFps: 4,
          canvasQuality: "0.4"
        }
      },
      debug: process.env.NODE_ENV === "development"
    });

    // Ensure recording is running (project settings still control what is stored).
    posthog.startSessionRecording();

    didInit = true;
  }, []);

  return null;
}

function PostHogPageView() {
  const pathname = usePathname();

  useEffect(() => {
    if (!didInit) return;
    if (typeof window === "undefined") return;

    posthog.capture("$pageview", { $current_url: window.location.href });
  }, [pathname]);

  return null;
}

export function AnalyticsProvider({ children }: { children: ReactNode }) {
  return (
    <PostHogProvider client={posthog}>
      <PostHogInit />
      <PostHogPageView />
      {children}
    </PostHogProvider>
  );
}

